export default {
	data() {
		return {
			loadingMoreFlag: true, //上滑加载开关
			loadingMore: 'loading', //上滑加载更多的状态
			noDataFlag:false,
			
			articleId: '',
			pageSize: 6,
			currentPage: 1,
			list: []
		};
	},
	onReachBottom() {
		if (this.loadingMore === 'loading') {
			//防止重复加载
			return;
		}
		this.loadingMoreFlag = true;
		this.loadingMore = 'loading'; //每次上滑都要从新设置status
		setTimeout(() => {
			this.getCouponList(); //让加载图标旋转0.8s后才请求
		}, 600);
	},
	onPullDownRefresh() {
		//下拉刷新页面将之前的数据初始化
		this.list = [];
		this.noDataFlag=false;
		this.currentPage = 1;
		this.getCouponList();
	},
	onLoad(e) {
		//当页面加载的时候调用接口获取内容
		setTimeout(() => {
			this.getCouponList();
		}, 600);
	},
	methods: {
		//领取优惠券
		getCoupon(cid) {
			let that = this;
			that.baseGet(
				that.U({
					c: 'coupons_api',
					a: 'user_get_coupon',
					q: {
						couponId: cid
					}
				}),
				function(res) {
					that.Tips({ title: '优惠券领取成功!快去使用吧~' });
				},
				function(res) {
					console.log(res);
				}
			);
		},
		//获取优惠券id列表
		getCouponList() {
			let that = this;
			that.baseGet(
				that.U({
					c: 'coupons_api',
					a: 'get_issue_coupon_list',
					q: {
						limit: that.pageSize,
						page: that.currentPage
					}
				}),
				function(res) {
					if (res.data.length == 0) {
						that.loadingMore = 'noMore';
						that.closeLoadingMoreFlag(); //0.8后将状态隐藏
						return;
					}
					for (let i = 0; i < res.data.length; i++) {
						that.getCouponDetail(res.data[i]);
					}
				},
				function(res) {
					console.log(res);
				}
			);
		},
		//0.8s后隐藏加载提示的信息
		closeLoadingMoreFlag() {
			setTimeout(() => {
				this.loadingMoreFlag = false;
				this.noDataFlag=true;
			}, 800);
		},
		//获取优惠券信息
		getCouponDetail(data) {
			let that = this;
			that.baseGet(
				that.U({
					c: 'coupons_api',
					a: 'get_coupon_info',
					q: {
						cid: data.cid
					}
				}),
				function(res) {
					uni.stopPullDownRefresh(); //当请求成功之后将下拉刷新关闭
					if (res.data.length == 0) {
						that.loadingMore = 'noMore';
						that.closeLoadingMoreFlag(); //0.8后将状态隐藏
						return;
					}
					that.currentPage++;
					that.loadingMore = 'more';
					var info = {
						data: data,
						res: res.data
					};
					that.list.push(info);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		}
	}
};
